import { Component, OnInit } from "@angular/core";
import { GwtThenStepUi } from "@soushians/rule";
import { FormGroup, FormControl } from "@angular/forms";
import { ThenShowBlockGwtStepParams } from "../authorize.gwt-then-step";
import { BehaviorSubject } from "rxjs";


@Component({
	template: `<mat-card class="step" fxLayout="row" fxLayoutGap="5px" fxLayoutAlign="start end" [formGroup]="formGroup">
  <div fxFlex="0 0 auto" class="step-text then">
    Ø¢ÙÚ¯Ø§Ù
  </div>
  <div fxFlex="0 0 auto" class="step-text">
    Ø§ÛÙ Ø¨Ø®Ø´ ÙØ§Ø¨Ù ÙÙØ§ÛØ´
  </div>
  <div fxFlex="nogrow">
    <mat-form-field fxFlex="nogrow">
      <mat-select formControlName="verb">
        <mat-option value="visible">Ø¨Ø§Ø´Ø¯</mat-option>
        <mat-option value="hidden">ÙØ¨Ø§Ø´Ø¯</mat-option>
      </mat-select>
    </mat-form-field>
  </div>
</mat-card>`,
	styles: [`:host{width:100%}`]
})
export class GwtThenAuthorizeStepComponent implements GwtThenStepUi<any> {
	formGroup: FormGroup;
	params$ = new BehaviorSubject<ThenShowBlockGwtStepParams>({} as ThenShowBlockGwtStepParams);
	set params(params) {
		if (this.formGroup) {
			this.formGroup.patchValue(params);
		}
		this.params$.next(params);
	}
	get params(): ThenShowBlockGwtStepParams {
		return Object.assign({}, this.formGroup.value);
	}
	constructor() {
		this.formGroup = this._create_formGroup();
		this.params$.subscribe(params => this.formGroup.patchValue(params));
	}

	/**
	 * private methods
	 */
	_create_formGroup() {
		return new FormGroup({
			verb: new FormControl("hidden")
		});
	}
}
